.flex {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}
@media (min-width: 43.75em) {
  .flex {
    flex-direction: row;
    justify-content: space-between;
  }
}

.flex > * {
  width: 100%;
}

.grid-ish {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 0.5em;
}

.grid-ish > * {
  flex-basis: 2.8125rem;
  flex-grow: 1;
}

.container {
  width: min(85%, 36rem);
  margin-inline: auto;
}

:root {
  --White: hsl(0, 0%, 93%);
  --Black: hsl(353, 100%, 2%);
  --Dark-Blue: hsl(229, 47%, 20%);
  --Light-Blue: hsl(224, 44%, 73%);
  --Light-Red: hsl(349, 93%, 64%);
}

:root {
  --fs-sm: 1.125rem;
  --fs-large: 2.5rem;
  --fs-x-large: 5.625rem;
  --ff-title: Verdana;
  --ff-number: "Orbitron", sans-serif;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
}

body {
  min-height: 100vh;
  display: grid;
  place-items: center;
  padding-block: 4rem;
}

button {
  background: none;
  color: inherit;
  border: none;
  padding: 0;
  font: inherit;
  cursor: pointer;
  outline: inherit;
}

html {
  font-size: 100%;
}

body {
  text-align: center;
}

h3 {
  font-size: var(--fs-large);
  color: var(--White);
  font-family: var(--ff-title);
}

.card__number,
.button {
  font-family: var(--ff-number);
}

.card__number {
  font-size: var(--fs-x-large);
  color: var(--Light-Red);
  border-radius: 0.5rem;
}

.button {
  font-size: var(--fs-sm);
  color: var(--Light-Blue);
}

.card {
  background-color: var(--Dark-Blue);
  padding: min(10vh, 4.375rem) min(10vh, 5.625rem);
  box-shadow: 10px 10px 15px rgba(0, 0, 0, 0.2);
}
.card__title {
  margin-bottom: 1.3125rem;
}
.card__number {
  background-color: var(--Black);
  margin-bottom: 1.625rem;
  padding-inline: 0.5rem;
}

.button {
  padding: 0.25em;
  border: 2px solid var(--Light-Blue);
  border-radius: 0.25rem;
}